.seat-chart {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  
  .row {
    display: flex;
    align-items: center;
  }
  
  .row-label {
    width: 30px;
    text-align: right;
    margin-right: 10px;
    font-weight: bold;
  }
  
  .seat-btn {
    width: 30px;
    height: 30px;
    margin: 2px;
    border-radius: 4px;
  }
  
  .btn-success {
    background-color: #28a745;
  }
  
  .btn-danger {
    background-color: #dc3545;
  }
  
  .btn-warning {
    background-color: #ffc107;
  }
  
  .btn-primary {
    background-color: #007bff;
  }
  